/* Reset default */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  font-family: "Roboto", Tahoma, Geneva, Verdana, sans-serif;
  /* color: white; */
  background-color: #156eac;
}

/* Buttons */

.btn {
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px 20px;
  margin: 0px 10px;
  background: #262626;
  color: white;
  font-size: 1em;
  min-width: 100px;
  text-align: center;
}

.btn:hover {
  opacity: 0.9;
}

.container {
  width: 90%;
  max-width: 1400px;
  margin: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}

.buttons {
  padding-top: 3em;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.buttons a {
  margin: 0px 20px;
}

.social-buttons {
  padding-top: 1em;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

